<script>
import Headerbar from '../components/Header'
import Actblock from '../components/Actblock'
import Searchbar from '../components/Searchbar'
// import Iconbtn from '../components/Iconbtn'
import axios from 'axios'
import Actmemberinfopage from '../components/ActMemberinfo'
export default {
    data() {
        return {
            isclick: false,
            tablist: ['全部', '已報名', '已取消'],
            currenttabindex: 0,
            regisinfo: [],
            listinfo: [],
            testlist: [],
            isopen: false,
            clickmemberid: 0,
            propsisopen: false,
            currentstatus: '全部',
            keyword: ''
        }
    },
    mounted() {
        console.log(this.$route.params.eventid)
        let url = "https://script.google.com/macros/s/AKfycbwnB-WiCD49DFBQKGb11VV_NlBl6RqPXuXtuCtcVm6xqxbxqP3Hm-_0ze9NFqvCcxDNfA/exec"
        axios({
                method: 'get',
                url: url,
            })
            .then((response) => {
                this.regisinfo = response.data.res[0]
                let newlist = response.data.res
                this.listinfo = newlist.filter(item => {
                    if (item[0] == this.eventinfo[0]) {
                        return item
                    }
                })
                console.log(this.listinfo)
            })
    },
    methods: {
        clickedtab(index, status) {
            this.currentstatus = status;
            this.currenttabindex = index;
        },
        openmemberinfo(id) {
            this.isopen = true;
            this.clickmemberid = id;
            document.body.scrollTop = document.documentElement.scrollTop = 0;
            this.propsisopen = true;
        },
        closememberifo() {
            this.isopen = false;
        },
        search(val) {
            this.keyword = val;

        }
    },
    computed: {
        eventinfo() {
            return this.$store.state.currentlistinfo;
        },
        filtermemberlist() {
            return this.listinfo.filter(item => {
                for (let i = 0; i < item.length; i++) {
                    if (this.currentstatus == "全部" && item[i].content.search(this.keyword) == 0) {
                        return item
                    } else {
                        if (item[i].content == this.currentstatus) {
                            return item
                        }
                    }

                }
                // item.forEach(element => {

                // });
                // if(this.currentstatus == item.)
            })
        }
    },
    components: {
        Headerbar,
        Actblock,
        Searchbar,
        Actmemberinfopage
    }
}
